import React, {Component} from 'react';
import {Card, CardBody, Progress} from 'reactstrap';
import imgLogo from '../../assets/img/invoice/logo-ip.png';
import imgInfo from '../../assets/img/invoice/banner.gif';
import imgQr from '../../assets/img/invoice/qr.png';
import stylePDF from './stylePDF.css';
import numeral from 'numeral';
import moment from 'moment';
import QRCode from 'qrcode';

const styles = {
  body: {
    position: 'relative',
    width: '20cm',
    height: '29.7cm',
    margin: '0 auto',
    color: '#001028',
    background: '#ffffff',
    fontFamily: 'Arial, sans-serif',
    fontSize: '12px',
    fontFamily: 'Arial',
    marginTop: '5px',
  },
  clearfix: {
    clear: 'both',
  },
  logo: {
    textAlign: 'center',
    height: '55px',
    padding: 8,
  },
  main: {
    marginTop: '24px',
  },
};
class InvoicePDF extends Component {
  constructor (props) {
    super (props);
    this.state = {
      imgQrGenerate: null,
    };
  }

  async componentWillMount () {
    const dataCFDI = this.props.data;
    const demoFolio = '0CE01D7D-6A48-4A98-B50C-E1A41127101E';
    console.log ('dataCFDI', dataCFDI);
    const Emisor = dataCFDI.cfdi && dataCFDI.cfdi.rfcem
      ? dataCFDI.cfdi.rfcem
      : null;
    const Receptor = dataCFDI.cfdi && dataCFDI.cfdi.rfcrec
      ? dataCFDI.cfdi.rfcrec
      : null;

    const urlSAT =
      'https://verificacfdi.facturaelectronica.sat.gob.mx/default.aspx?id=' +
      demoFolio +
      '&re=' +
      Emisor.Rfc +
      '&rr=' +
      Receptor.Rfc +
      '&tt=208800.00&fe=K8nO6w==';
    this.generateQR (urlSAT);
  }

  generateQR = async text => {
    try {
      this.setState ({
        imgQrGenerate: await QRCode.toDataURL (text),
      });
    } catch (err) {
      console.error (err);
    }
  };

  render () {
    const data = this.props.data;

    if (data) {
      return (
        <div id="invoicePrintPDF" style={styles.body}>
          <div style={styles.clearfix}>
            <div style={styles.logo}>
              <img src={imgLogo} style={{width: '140px', float: 'left'}} />
              <div className="company">
                <div>EFECTO DEL COMPROBANTE: INGRESO</div>
                <div>
                  FECHA :{moment (data.createdAt).format ('YYYY-MM-DD')}
                </div>
                <div>
                  <a href="mailto:contacto@ipco.com">contacto@ipco.com</a>
                </div>
              </div>
            </div>
            <hr />
            <div className="company">
              <div><span style={{fontWeight: 'bold'}}>IPC170427K40</span></div>
              <div>
                INTEGRA POTENCIA CONSULTORIA EN <br />
                HIDROC ARBUROS Y ELECTRICIDAD <br /> SA DE CV
              </div>
            </div>
            <div className="project">
              <div><span>NOMBRE</span> {data.subsidiariesId.nombre}</div>
              <div>
                <span>RFC :</span> {data.subsidiariesId.direccionFactura.rfc}
              </div>
              <div><span>USO CFDI</span> Gastos en general</div>
              <div><span>CUCC</span> {data.subsidiariesId.cucc}</div>
            </div>
          </div>
          <div style={styles.main}>
            <table style={styles.main}>
              <thead>
                <tr>
                  <th className="service">CONCEPTO</th>
                  <th className="desc">Precio</th>
                  <th>TOTAL</th>
                </tr>
              </thead>
              <tbody>
                {data.conceptos.length != 0
                  ? data.conceptos.map ((item, key) => {
                      return (
                        <tr key={key}>
                          <td
                            style={{padding: '2px', margin: '3px'}}
                            className={stylePDF.service}
                          >
                            {item.concepto == 'SubtotalA' ||
                              item.concepto == 'SubtotalB'
                              ? <p style={{fontWeight: 'bold', margin: '3px'}}>
                                  {item.concepto}
                                </p>
                              : <p style={{margin: '3px'}}>
                                  {item.concepto}
                                </p>}
                          </td>
                          <td
                            style={{padding: '2px'}}
                            className={stylePDF.desc}
                          >
                            <p style={{margin: '3px'}}>
                              {item.lectura}
                            </p>
                          </td>
                          <td
                            style={{padding: '2px'}}
                            className={stylePDF.total}
                          >
                            {item.concepto == 'SubtotalA' ||
                              item.concepto == 'SubtotalB'
                              ? <p style={{fontWeight: 'bold', margin: '3px'}}>
                                  ${numeral (item.total).format ('0.00')}
                                </p>
                              : <p style={{margin: '3px'}}>
                                  ${numeral (item.total).format ('0.00')}
                                </p>}
                          </td>
                        </tr>
                      );
                    })
                  : <tr>
                      <td
                        colSpan={4}
                        className={(stylePDF.grand, stylePDF.total)}
                      >
                        SIN INFORMACIN
                      </td>
                    </tr>}
                <tr>
                  <td colSpan={2}>SUBTOTAL</td>
                  <td className={stylePDF.total}>
                    {' '}
                    $
                    {' '}
                    {numeral (data.breakdown.subtotal).format ('0.00')}
                  </td>
                </tr>
                <tr>
                  <td colSpan={2}>IVA</td>
                  <td className={stylePDF.total}>
                    {' '}
                    $
                    {' '}
                    {numeral (data.breakdown.iva).format ('0.00')}
                  </td>
                </tr>
                <tr>
                  <td colSpan={2} className={(stylePDF.grand, stylePDF.total)}>
                    TOTAL
                  </td>
                  <td className={(stylePDF.grand, stylePDF.total)}>
                    <strong>
                      ${numeral (data.breakdown.total).format ('0.00')}
                    </strong>
                  </td>
                </tr>
              </tbody>
            </table>
            <div style={{width: '100%'}}>
              <img
                src={this.state.imgQrGenerate}
                style={{width: '150px', float: 'left'}}
              />
              <div style={{width: '80%', display: 'inline-block'}}>
                <span
                  style={{
                    color: '#5d6975',
                    textAlign: 'left',
                    width: '100%',
                    marginRight: '10px',
                    display: 'inline-block',
                    fontSize: '1.1em',
                  }}
                >
                  Sello digital del CFDI:
                </span>
                <span style={{overflowWrap: 'break-word'}}>
                  {data.cfdi && data.cfdi.sello ? data.cfdi.sello : ''}
                </span>
              </div><br />
            </div>
            {/* <div id="notices" style={{marginTop: '45px'}}>
              <div>NOTA:</div>
              <div className="notice" />
            </div> */}
            <div>
              <img src={imgInfo} style={{width: '100%'}} />
            </div>
          </div>
          <footer>
            Este documento es una representacin impresa de un CFDI.{' '}
          </footer>
        </div>
      );
    }
  }
}
export default InvoicePDF;
