import React, {Component} from 'react';
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import feathers from '../../feathers';

import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';
import Select from 'react-select';
import moment from 'moment';
import numeral from 'numeral';
import InvoicePDF from './InvoicePDF';
import html2canvas from 'html2canvas';
import jsPDF from 'jspdf';

class Invoice extends Component {
  constructor (props) {
    super (props);
    this.state = {
      data: [],
      pdf: false,
      total: '',
      selectedEmpresa: null,
      empresas: [],
      idInvoice: null,
    };

    this.toggleInvoicePDF = this.toggleInvoicePDF.bind (this);
  }

  toggleInvoicePDF () {
    this.setState ({
      pdf: !this.state.pdf,
    });
  }

  toggleInvoicePDFData (data) {
    if (data) {
      this.setState ({
        pdf: !this.state.pdf,
        dataInvoice: data,
      });
    } else {
      this.setState ({
        pdf: !this.state.pdf,
      });
    }
  }

  async componentWillMount () {
    let _this = this;
    let invoiceResult = await feathers.service ('invoices').find ({
      query: {
        $populate: 'subsidiariesId',
      },
    });
    let empresas = await feathers.service ('clients').find ({});

    empresas = empresas.data.map (empresa => {
      return {
        value: empresa._id,
        label: empresa.nombre,
      };
    });

    console.log ('invoiceResult', invoiceResult);

    this.setState ({
      data: invoiceResult.data,
      empresas: empresas,
    });
  }

  onChangeEmpresa = value => {
    this.setState ({
      selectedEmpresa: value,
    });
  };

  printDocument = () => {
    const input = document.getElementById ('invoicePrintPDF');
    html2canvas (input, {scale: 1}).then (canvas => {
      const imgData = canvas.toDataURL ('image/png');
      const pdf = new jsPDF ();
      pdf.addImage (imgData, 'JPEG', 5, 15);
      // pdf.output ('dataurlnewwindow');
      pdf.save ('factura-' + moment ().format ('YYYY-MM-DD') + '.pdf');
    });
  };

  render () {
    let indexNumber = 0;
    const {empresas, selectedEmpresa} = this.state;
    const valueFromIdCompany = id => empresas.find (o => o.value === id);
    const columns = [
      {
        dataField: 'subsidiariesId.nombre',
        text: 'Centro de Carga',
        sort: true,
        headerStyle: (colum, colIndex) => {
          return {width: 200};
        },
        formatter: (cell, row) => {
          return cell;
        },
      },
      {
        dataField: 'breakdown.total',
        text: 'Total Facturado',
        sort: true,
        formatter: (cell, row) => {
          return numeral (cell).format ('0.00');
        },
      },
      {
        dataField: 'fecha_inicio',
        text: 'Periodo',
        sort: true,
        formatter: (cell, row) => {
          return (
            moment (row.fecha_inicio).add (1, 'days').format ('DD-MM-YYYY') +
            ' - ' +
            moment (row.fecha_fin).add (1, 'days').format ('DD-MM-YYYY')
          );
        },
      },
      {
        dataField: 'actions',
        text: 'Acciones',
        headerStyle: (colum, colIndex) => {
          return {width: 150};
        },
        formatter: (cell, row) => {
          return (
            <Row>
              <Col col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                <Button
                  color="success"
                  className="btn-square pull-right"
                  onClick={() => {
                    this.toggleInvoicePDFData (row);
                  }}
                >

                  PDF
                </Button>
                {'   '}
                <Button
                  color="warning"
                  className="btn-square pull-right"
                  onClick={() => {
                    const xmlpath =
                      process.env.REACT_APP_URL_FEATHERS + '/cfdi/' + row._id;
                    window.open (xmlpath);
                  }}
                >

                  XML
                </Button>
              </Col>
            </Row>
          );
        },
      },
    ];

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" lg="12">
            <Card>
              <CardHeader>
                Administracin - Facturacin
                <div className="card-header-actions">
                  <i className="fa fa-download" />
                  <a href="http://ipco.com" className="card-header-action">
                    <small className="text-muted">EXPORTAR</small>
                  </a>
                </div>
              </CardHeader>
              <Row
                className="pull-right"
                style={{
                  marginTop: '5px',
                  paddingRight: '5px',
                  paddingLeft: '5px',
                }}
              >
                <Col col="12" sm="4" md="4">
                  <Select
                    placeholder="-- Filtrar por Cliente"
                    name="empresa"
                    options={empresas}
                    value={valueFromIdCompany (selectedEmpresa)}
                    onChange={event => this.onChangeEmpresa (event.value)}
                  />
                </Col>
                <Col col="12" sm="8" md="8">
                  <Button
                    onClick={() =>
                      this.props.history.push ('/facturacion/form')}
                    color="success"
                    className="btn-square pull-right"
                  >
                    <i className="fa fa-plus" />{' '}
                    Agregar Facturacin
                  </Button>
                </Col>
              </Row>
              <Row>
                <CardBody style={{paddingTop: 8}}>
                  <BootstrapTable
                    keyField="_id"
                    data={this.state.data}
                    columns={columns}
                    pagination={paginationFactory ()}
                    bootstrap4
                    striped
                    hover
                  />
                </CardBody>
                <Modal
                  isOpen={this.state.pdf}
                  toggle={this.toggleInvoicePDF}
                  className={'modal-lg ' + this.props.className}
                >
                  <ModalHeader toggle={this.toggleInvoicePDF}>
                    Factura - Folio 001
                  </ModalHeader>
                  <ModalBody>
                    <InvoicePDF data={this.state.dataInvoice} />
                  </ModalBody>
                  <ModalFooter>
                    <Button color="primary" onClick={this.toggleInvoicePDF}>
                      Reenviar por correo
                    </Button>{' '}
                    <Button color="primary" onClick={this.printDocument}>
                      Imprimir PDF
                    </Button>
                    {' '}
                    <Button color="secondary" onClick={this.toggleInvoicePDF}>
                      Cancelar
                    </Button>
                  </ModalFooter>
                </Modal>
              </Row>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Invoice;
